import React from 'react';
import autorImage from '../../assets/profile_images/profile_image_mirjana.JPG';

function BlogFour() {
  return (
    <div className="blog blog-four">
      <h3>Koje vetine treba da razvije uspean tim</h3>
      <p className="first-p">
        Pored individualnih vetina, postoje vetine koje se razvijaju timski, a
        koje su kljune za novi pojam, a to je razvoj emocionalno inteligentnih
        timova.
      </p>
      <ol>
        <li>
          <p>Timska samoprocena</p>
          <p>
            Izuzetno je znaajno za svakog lidera da odredi vreme za ispitivanje
            efikasnosti tima ukljuivajui tim u ove aktivnosti. Kreiranje
            merljivih zadataka, a zatim njihovo obradjivanje i merenje, treba
            obavljati transparentno i uz timsko razumevanje oba procesa.
            Prenoenje i komuniciranje oseanja izmedju lanova tima kree u
            osnovi od otvorene komunikacije lidera i njegove sposobnosti da
            iskae oseanja vezana za tim i situacije kroz koje tim prolazi.
            Kljuno je dozvoliti lanovima tima da daju miljenje i predloge na
            procese koje smatraju neefikasnim ili nedovoljno efikasnim.
          </p>
        </li>
        <li>
          <p>Feedback</p>
          <p>
            Znaaj feedback-a ogleda se prvenstveno u jasnoj proceni, otvorenoj
            komunikaciji i poverenju izmedju lanova tima.
          </p>
        </li>
        <li>
          <p>Stvaranje resursa za rad s emocijom</p>
          <p>
            Potrebno je otvoreno razgovarati o tekim problemima i bavite se
            emocijama koje okruuju tim, kao i pronai kreativne naine za
            izraavanje emocija u grupi.
          </p>
        </li>
        <li>
          <p>Reavanje problema proaktivno</p>
          <p>
            Emotivno inteligentni timovi mogu predvideti probleme. Za taj
            segment znaajno je preuzimanje inicijative kako bi tim razumeo
            situaciju i bio efikasniji. Promene su neminovne, potrebne,
            oekivane, ali one mogu voditi u dva pravca: rast ili krizu. Jasno
            je da dobre odluke vode ka rastu kompanije, a da lose odluke vode ka
            krizi.
          </p>
        </li>
        <li>
          <p>Stvaranje afirmativnog okruenja</p>
          <p>
            Poslednja taka ali od velikog znaaja jeste stvaranje afirmativnog
            okruenja. Ovde optimizam lidera dolazi do izraaja. Bitno je da
            lider podstie vanu i pozitivnu misiju grupe i da se usredsredi na
            ono to moe da kontrolie. Pravo lideristvo se bazira na ideji da
            je potrebno da lideri emotivno dopru do svojih sledbenika i motiviu
            ih putem poistoveivanja zajednikih vrednosti i vie svrhe (Bass et
            al. 1987).
          </p>
        </li>
      </ol>
      <div className="blog-info">
        <p>Autor teksta:</p>
        <div className="blog-autor">
          <img src={autorImage} alt="Slika autora bloga  Mirjana Matic" />
          <div className="blog-autor-info">
            <p>Mirjana Mati</p>
            <p>Founder &amp; Director BFS Company</p>
          </div>
        </div>
      </div>
    </div>
  );
}

export default BlogFour;
