import React from 'react';
import autorImage from '../../assets/profile_images/profile_image_mirjana.JPG';

function BlogThree() {
  return (
    <div className="blog">
      <h3>Emocionalna inteligencija i krizne situacije</h3>
      <p className="first-p">
        Kada govorimo o emocionalnoj inteligenciji u biznisu, najpre moramo
        spomenuti lanak autora Golemana iz 1998 godine, koji je objavljen nakon
        sprovedenih istraivanja i koji daje zakljuak da se 67% svih vetina
        koje su potrebne pojedincu kako bi bio uspean u poslovnim rezultatima,
        zapravo zasniva na emocionalnoj inteligenciji. U svom istraivanju
        Goleman je podelio vetine zaposlenog na line sposobnosti i socijalne
        sposobnosti. Prvoj grupi pripadaju sposobnosti kao to su: samosvesnost,
        upravljanje sobom i svojim reakcijama, zatim motivacija. Drugoj grupi
        pripadaju sposobnosti kao to su: empatija i socijalne vetine, odnosno
        spretnost u budjenju poeljnih reakcija kod drugih.
      </p>
      <p className="second-p">
        Paralelno, istraiivanja vezana za krizni menadment pokazala su da u
        periodu krize svi lideri koji su ukljueni u reavanje iste, koriste
        svoje kognitivne resurse kako bi reili krizne situacije, bavei se
        svakodnevno problemima krize, esto ne ukljuujui bilo kakve druge
        resurse (Bargh &amp; Chartrand, 1999). Istaivanje koje su obavili Mader
        i Smith (2009) utvrdilo je tanost hipoteze da efekat emocija lidera u
        kriznim situacijama na vrednovanje lidera ostvaruje se preko efekta
        uesnika. Ovom studijom je dokazano da prihvatanje odgovornosti ima
        veliki uticaj na lanove time, odnosno da lider koji prihvata
        odgovornost u krizi je vie cenjen od strane svojih zaposlenih. Takoe,
        dokazano je da lider koji daje odgovore i pokazuje emocije u kriznim
        situacijama utie na vrednovanje sebe kao vodje od strane lanova tima
        (Arsovski 2014).
      </p>
      <p>
        Jasno je da vetine potrebne za efikasno upravljanje kriznim situacijama
        vremenom dobijaju na znaaju. Jedan od razloga jeste i oseaj da
        savremeno doba donosi uestale periode krize, odnosno da se danas svaka
        nepovoljna situacija moe smatrati kriznom situacijom. Emocionalna
        inteligencija lidera daje irok spektar razliitih socijalnih i linih
        vetina, koje su, moemo rei u dananjem modelu poslovanja, postale
        neophodne, a ega su postale svesne i same organizacije.
      </p>
      <div className="blog-info">
        <p>Autor teksta:</p>
        <div className="blog-autor">
          <img src={autorImage} alt="Slika autora bloga  Mirjana Matic" />
          <div className="blog-autor-info">
            <p>Mirjana Mati</p>
            <p>Founder &amp; Director BFS Company</p>
          </div>
        </div>
      </div>
    </div>
  );
}

export default BlogThree;
