import React from 'react';
import blogOneImg from '../../assets/blog_images/blog_one_image.png';
import autorImage from '../../assets/profile_images/profile_image_mirjana.JPG';

function BlogOne() {
  return (
    <div className="blog">
      <h3>PROSEK JE LEPA ZONA KOMFORA - KAKO IZAZVATI SEBE NA IZVRSNOST?</h3>
      <p className="first-p">
        Nakon 9 godina u korporativnom svetu donela sam odluku da pokrenem
        sopstveni biznis. Moram priznati da sam ja oduvek o tome razmiljala,
        ali kada sam zavrila fakultet imala sam elju za dokazivanjem i nisam
        smatrala da sam spremna za takav korak (tada sam mislila da moda nikada
        ni neu biti spremna). Kada imate potrebu da se dokazujete drugima onda
        je korporativni svet jako pogodno mesto, medjutim kada to preraste u
        potrebu da se dokazujete samom sebi, tada prirodno dolazi do odluke da
        postanete preduzetnik. Jer, u preduzetnitvu sve je u tome, kako da
        svaki dan budete bolji nego jue, a da pritom to ne meri i ne ocenjuje
        niko sa strane, nego iskljuivo vi. Dakle samomotivacija je veoma bitna
        u tom procesu. Kada razvijate spostveni biznis, ne moete biti u zoni
        komfora, jer ako ste proseni u industriji u kojoj poslujete teko da
        ete opstati. Medjutim, pre nego to u doneti odluku da postanem
        preduzetnik, bila sam u jednoj fazi, slobodno mogu rei zbunjenosti i
        imala sam potrebu da dodjem do odgovora ta je moj sledei korak u
        karijeri. S obzirom da godinama radim u podruju finansija, moja potreba
        bila je da taj odgovor dobijem u brojkama, ukoliko je mogue. Tada sam
        sela i napravila Matricu ciljeva koja me je izazvala na izvrsnost i
        dala mi neophodne odgovore u tom trenutku, a zatim me izbacila iz faze
        zbunjenosti i ubacila u fazu totalno van zone komfora.
      </p>
      <p>Hajde da vidimo kako matrica radi...</p>
      <p>
        1. Prvi korak je definisati svoju listu ciljeva (moja lista je imala 9
        ciljeva ali zapravo broj nije bitan).
      </p>
      <p>
        2. Drugi korak je da svim definisanim ciljevima dodelite broj u dve
        liste, gde broj 1 nosi najveu vrednost;
        <ul>
          <li>
            I. Lista Ciljevi koji imaju najvei uticaj na va ivot gde broj 1
            predstavlja cilj sa najveim uticajem, a zatim naredni brojevi sve
            manji uticaj u odnosu na prethodni.
          </li>
          <li>
            II. Lista Ciljevi koji su najizazovniji za vas gde takodje broj 1
            predstavlja cilj koji je najizazovniji, a zatim naredni brojevi sve
            manje izazovni ciljevi u odnosu na prethodni.
          </li>
        </ul>
        Dakle, svi vai ciljevi moraju da se nadju i dobiju broj u okviru obe
        ove liste, kako bismo ih ukrstili i time dobili rezultat. Sada kada ste
        vaim ciljevima dodelili brojeve, idemo na sledei korak...
      </p>
      <p>3. Trei korak je uneti ciljeve u ovu matricu i oitati rezultate;</p>
      <img
        className="blog-image"
        src={blogOneImg}
        alt="Slika,prosek je lepa zona komfora"
      />
      <p>
        Putem ove matrice ja sam dola do odluke da krenem u ostvarivanje
        ciljeva iz kvadrata 3, onih ciljeva kojih se najvie plaim ali ije
        ostvarenje bi mi donelo najvie ivotnog zadovoljstva.
      </p>
      <p>
        Nadam se da ste spremni da i sami prodjete kroz ovu evaluaciju svojih
        ciljeva i napravite promene i planove za naredne korake. elim vam puno
        uspeha u tome.
      </p>
      <div className="blog-info">
        <p>Autor teksta:</p>
        <div className="blog-autor">
          <img src={autorImage} alt="Slika autora bloga  Mirjana Matic" />
          <div className="blog-autor-info">
            <p>Mirjana Mati</p>
            <p>Founder &amp; Director BFS Company</p>
          </div>
        </div>
      </div>
    </div>
  );
}

export default BlogOne;
