import React from 'react';
import autorImage from '../../assets/profile_images/profile_image_mirjana.JPG';

function BlogTwo() {
  return (
    <div className="blog">
      <h3>Direktor/ka pre 30-te: trend ili eksperiment 21.veka?</h3>
      <p className="first-p">
        ta biste Vi rekli na ovo? Verujem da imate u svom okruenju bar jednu
        osobu koja je uspela da se pre 30te godine popne na neku od visokih
        menaderskih pozicija. Pre samo 30 godina tako neto bilo je teko i
        zamisliti. Na rukovodeim pozicijama bili su iskljuivo ljudi sa velikim
        iskustvom, a uz to iskustvo ilo je i znanje. Kao to znamo 21.vek i
        savremeno doba je doba informacija, pa se esto ispred iskustva i
        znanja, stavljaju brzina kojim se dolazi do infomacija kao i umee rada
        sa tehnologijama. Takodje, brzina kojom se deavaju promene u svetu
        dovele su do toga da poslodavci trae izuzetno energine ljude da vode
        biznis sektore, a smirenost takodje dolazi u drugi plan, to daje
        prednost mladjim osobama u odnosu na starije i iskusnije.
      </p>
      <p className="second-p">
        Sa jedne strane, ovaj eksperiment pokazao se izuzetno u mnogim
        kompanijama irom sveta, jer su mladi ljudi pokazivali veliku motivaciju
        u radu na tim pozicijama, kao i veu inicijativu i kreativnost, ali
        takodje i dosta vie greaka u radu. Dolazimo do onoga to je
        najznaajnije, veliki procenat greaka utie na samopouzdanje, pa su
        upravo ovi zaposleni ee bivali u burnout sindrom-u. Poznato je da
        menaderske pozicije podrazumevaju u najveoj meri bavljenje ljudima.
        Savremena istraivanja pokazuju da emocionalna inteligencija utie na
        sposobnost vodjenja tima, a da koeficijent emocionalne inteligencije
        raste godinama ivota i radnog iskustva. Dolazimo do zakljuka, da je
        priprodan tok stvari da stariji menaderi bolje vode timove jer imaju
        vei stepen emocionalne inteligencije.
      </p>
      <p>
        Sloiete se da nije lako dati odgovor na postavljeno pitanje, ali
        pokazalo se da najbolje rezultate su ostvarivali mladji menaderi na
        viim pozicijama uz mentorstvo starijih menadera. Dakle, bez timskog
        rada teko je zamisliti da jedna organizacija moe napredovati, pa bismo
        mogli rei da je i u ovom sluaju to dobitna kombinacija.
      </p>
      <div className="blog-info">
        <p>Autor teksta:</p>
        <div className="blog-autor">
          <img src={autorImage} alt="Slika autora bloga  Mirjana Matic" />
          <div className="blog-autor-info">
            <p>Mirjana Mati</p>
            <p>Founder &amp; Director BFS Company</p>
          </div>
        </div>
      </div>
    </div>
  );
}

export default BlogTwo;
