import React from 'react';
import { useRef, useEffect } from 'react';
import { motion, useInView, useAnimation } from 'framer-motion';

function Bfs() {
  const bfs = useRef();
  const ref = useRef(null);
  const isInView = useInView(ref);
  const mainControls = useAnimation();
  const pOneControls = useAnimation();
  const bgControl = useAnimation();

  useEffect(() => {
    if (isInView) {
      mainControls.start('visible');
      pOneControls.start('visible');
      bgControl.start('visible');
    }
  }, [isInView]);

  return (
    <div className="bfs" ref={bfs} id="bfs">
      <div className="section-name" ref={ref}>
        <motion.h1
          variants={{
            hidden: { scale: 0, opacity: 0 },
            visible: { scale: 1, opacity: 1 },
          }}
          initial="hidden"
          animate={bgControl}
          transition={{ duration: 0.5, delay: 0.5 }}
        >
          BFS company
        </motion.h1>
      </div>
      <motion.div
        className="border"
        variants={{
          hidden: { scale: 0, opacity: 0 },
          visible: { scale: 1, opacity: 1 },
        }}
        initial="hidden"
        animate={bgControl}
        transition={{ duration: 1, delay: 0.5 }}
      >
        <motion.div
          className="left-image"
          variants={{
            hidden: { x: -150, opacity: 0 },
            visible: { x: 0, opacity: 1 },
          }}
          initial="hidden"
          animate={mainControls}
          transition={{ delay: 1, duration: 1 }}
        ></motion.div>
        <div className="content">
          <motion.p
            className="main-text"
            variants={{
              hidden: { y: 50, opacity: 0 },
              visible: { y: 0, opacity: 1 },
            }}
            initial="hidden"
            animate={pOneControls}
            transition={{ delay: 1.5, duration: 1 }}
          >
            Naa osnovna delatnost je analiza postojeih, kreiranje novih, a
            zatim i implementacija poslovnih reenja u domenu finansijskog
            kontrolinga i menadmenta. Pored toga nudimo usluge savetovanja,
            istraivanja i edukacije. Na pristup klijentu je mikro prisup koji
            se ogleda kroz razumevanje konkretnih potreba i kreiranje reenja
            koja su prilagodjena tano poslovanju klijenta. Sa druge strane,
            nai proizvodi u delu edukacija sadre konzistentan pristup, to
            znai da klijent moe dobiti taan uvid u program koji je unapred
            definisan i odreen temom. Ponosni smo to posebne programe nudimo
            malim preduzetnicima i fizikim licima u elji da dodatno unapredimo
            njihova znanja i poslovanje i da uestvujemo u njihovom razvoju i
            uspehu, kao i velikim kompanijama kojima pruamo potpunu kooperaciju
            u navigaciji kroz procesne promene i razvoj zaposlenih. Na uspeh
            ogleda se u strunosti i znanju naih zaposlenih i svih spoljnih
            saradnika, zatim u naoj zajednikoj sinergiji koja je motivisana da
            naim klijentima damo dodatnu vrednost.
          </motion.p>
          <div className="bottom-text">
            <motion.div
              className="text-left"
              variants={{
                hidden: { y: 50, opacity: 0 },
                visible: { y: 0, opacity: 1 },
              }}
              initial="hidden"
              animate={pOneControls}
              transition={{ delay: 1.5, duration: 1 }}
            >
              <h3>NAA MISIJA</h3>
              <ul className="nasa_misija">
                <li>
                  Razumeti potrebe klijenata, kao i promene drutva i trita
                </li>
                <li>Oslukivati trite i davati inovativne pristupe</li>
                <li>
                  Ulagati u zaposlene i spoljne saradnike, podsticati iskrenost
                  i timski duh
                </li>
                <li>
                  Graditi brend od poverenja koji se prepoznaje kroz kvalitet i
                  autentinost
                </li>
              </ul>
            </motion.div>
            <motion.div
              className="text-right"
              variants={{
                hidden: { y: 50, opacity: 0 },
                visible: { y: 0, opacity: 1 },
              }}
              initial="hidden"
              animate={pOneControls}
              transition={{ delay: 1.7, duration: 1 }}
            >
              <h3>NAA VIZIJA</h3>
              <p>
                Naa vizija je da budemo sinonim za kompaniju koja pomera
                granica u pogledu vrednosti koje prua svojim klijentima, uei
                iz svakog novog iskustva i kreirajui pozitivne promene i
                trendove u oblasti finansijskog kontrolinga i menadmenta.
              </p>
            </motion.div>
          </div>
        </div>
      </motion.div>
    </div>
  );
}

export default Bfs;
